@import url('https://fonts.googleapis.com/css2?family=Inter:wght@200;300;500&display=swap');

/* Offering full page access */
.offering-main-container {
    background-color: #ffffff;
    font-family: 'Inter', sans-serif;
}

.main-section-right {
    /* border: 1px solid red; */
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40%;
}

/* Section - 1 */
.offering-svg {
    background-color: darkgrey;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* flex-flow: column wrap; */
    height: 60vh;
    width: 60vh;
    padding: 10vh;
    border-radius: 125%;
}

.offering-svg h3{
    margin: 10px;
    text-align: center;
}

.offering-section-1-container {
    height: 100vh;
    padding-top: 10vh;
    /* background-color: #000000;
    color: #ffffff; */
}

.offering-section-1-image {
    /* background-image: url("https://cdn-icons-png.flaticon.com/512/3915/3915655.png"); */
    background-repeat: no-repeat;
    background-position: center;
    /* background-size: cover; */
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    height: 90vh;
    padding: 20px 50px;
    /* border: 1px solid red; */
}

.section-1-offering-heading1 {
    font-size: 40px;
    text-align: center;
    height: 40%;
}

.section-1-offering-heading1 h3 {
    font-weight: 600;
}

.section-1-offering-heading2 {
    display: flex;
    flex-wrap: wrap;
    /* justify-content: center;
    align-items: center; */
    /* border: 1px solid yellow; */
}

.offering-section-1-sub-heading {
    /* border: 1px solid green; */
    /* margin: 30px 150px; */
    padding: 5px;
    font-size: 35px;
}

.offering-section-1-sub-heading h3 {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    cursor: pointer;
}

.section-1-offering-heading3 {
    margin: 10px;
    padding: 10px;
    font-size: 22px;
    text-align: center;
}

.section-1-offering-heading3 h3 {
    font-weight: 300;
}

/* Section - 2 */
.offering-section-2-container {
    height: 100vh;
    /* padding-top: 10vh; */
    background-color: #000000;
    color: #ffffff;
    display: flex;
    align-items: center;
}

.offering-section-2-content {
    margin: 20px;
    padding: 10px 150px;
    display: flex;
}

.offering-section-2-content p {
    font-size: 60px;
    font-weight: 800;
}

/* Section - 3 */
.offering-section-3-container {
    height: 100vh;
    padding: 0 250px;
    padding-top: 4vh;
    padding-bottom: 40px;
    background-color: #000000;
    color: #ffffff;
    display: flex;
    flex-wrap: wrap;
    /* align-items: center; */
    justify-content: center;
}

.offering-benifit-item-container1 {
    margin: 2px;
    padding: 8px;
    display: flex;
    flex-direction: column;
    width: 240px;
    /* border: 1px solid red; */
}

.benifit-item-number {
    padding: 4px;
    margin: 2px;
    font-size: 40px;
    color: grey;
}

.benifit-item-name {
    padding: 4px;
    margin: 2px;
    font-size: 26px;
}
